.login {
    background-color: hsl(218, 41%, 15%);
    background-image: radial-gradient(650px circle at 0% 0%,
            hsl(218, 41%, 35%) 15%,
            hsl(218, 41%, 30%) 35%,
            hsl(218, 41%, 20%) 75%,
            hsl(218, 41%, 19%) 80%,
            transparent 100%),
        radial-gradient(1250px circle at 100% 100%,
            hsl(218, 41%, 45%) 15%,
            hsl(218, 41%, 30%) 35%,
            hsl(218, 41%, 20%) 75%,
            hsl(218, 41%, 19%) 80%,
            transparent 100%);
    height: 100vh;
    width: 100vw;
}

#round {
    position: fixed;
    top: 10%;
    left: 35%;
    height: 350px;
    width: 350px;
    background: radial-gradient(#44006b, #ad1fff);
    overflow: hidden;
    border-radius: 50%;
}

#irregular {
    position: fixed;
    bottom: 20%;
    right: 10%;
    width: 300px;
    height: 300px;
    border-radius: 38% 62% 63% 37% / 70% 33% 67% 30%;
    background: radial-gradient(#44006b, #ad1fff);
    overflow: hidden;
}

.login-content {
    width: 100%;
    height: 100%;
    display: flex;
}

.content-left {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
}

.content-right {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
}

.login-form {
    width: 75%;
    flex-direction: column;
    padding: 25px;
    border-radius: 10px;
    background: white;
    opacity: 0.7;
}

.login-text {
    padding: 0px 8vw 0px 3vw;
}

.login-text h3 {
    color: hsl(218, 81%, 95%);
    font-size: 40px;
}

.login-text h2 {
    color: hsl(218, 81%, 75%);
    font-size: 40px;
    padding-bottom: 5vh;
}

.login-text p {
    text-align: justify;
    font-size: 18px;
    color: hsl(218, 81%, 85%)
}


.form-controls {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 15px;
}

.form-controls h4{
    cursor: pointer;
}

.login-bar {
    width: 100%;
    margin-top: 2%;
}

.login-bar p {
    width: "100%";
    padding-left: 2%;
    padding-right: 2%;
    font-weight: 900;
}

.social-login  {
    padding-top: 2%;
    width: 20%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 2%;
}

.social-login i {
    font-size: 25px;
    cursor: pointer;
}

@media (max-width: 676px) {
    .content-left {
        display: none;
    }

    .content-right {
        width: 100%;
        display: flex;
        justify-content: center;
    }
    
    .login-form  {
        width: 90%;
    }

    #round {
        top: 10%;
        left: 35%;
        height: 250px;
        width: 250px;
    }

    #irregular {
        bottom: 15%;
        right: 20%;
        width: 250px;
        height: 250px;
    }

    .social-login  {
        width: 40%;
    }
}

@media (max-width: 992px) {
    #round {
        top: 10%;
        left: 45%;
        height: 250px;
        width: 250px;
    }

    #irregular {
        bottom: 15%;
        right: 3%;
        width: 250px;
        height: 250px;
    }

    .login-form  {
        width: 90%;
    }

    .login-text {
        padding: 0px 8vw 0px 3vw;
    }
    
    .login-text h3 {
        color: hsl(218, 81%, 95%);
        font-size: 35px;
    }
    
    .login-text h2 {
        color: hsl(218, 81%, 75%);
        font-size: 33px;
        padding-bottom: 4vh;
    }
    
    .login-text p {
        text-align: justify;
        font-size: 16px;
        color: hsl(218, 81%, 85%)
    }
    
    .social-login  {
        width: 40%;
    } 
}

